@import url(http://fonts.cdnfonts.com/css/ttvtechprecomput);
body{
  font-family: 'TTVTechPrecomput', sans-serif;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.container{
    text-align: center;
    display: flex;
    flex-direction: column;
    background-image: "https://www.gratistodo.com/wp-content/uploads/2016/09/Pokemon-5.jpg";
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;  
    height: 100vh;
    font-size: 50px;
    font-weight: bold;
}

.logoLand{
    width: 700px ;
}


.tittle{
    font-family: 'TTVTechPrecomput', sans-serif; 
    color: #252422;
}

.content{
    font-family: 'TTVTechPrecomput', sans-serif;
    color: #252422;
    padding: 2rem;
}

.btn-land{
        border-radius: 35px;
        border: 5px solid #00008B;
        list-style: none;
        color:#252422;
        font-family: 'TTVTechPrecomput', sans-serif;
        font-weight: bold;
        padding: 12px 16px;
        margin: 0 8px;
        position: relative;
        cursor: pointer;
        white-space: nowrap; 
        padding: 8px 20px;
        outline: none;
        cursor: pointer;
        font-size: 40px;
        background-color: #FFD700;
    }


.btn-land::before {
        content: " ";
        position: absolute;
        border-radius: 35px;
        top: 0;
        left:0;
        height:100%;
        width: 100%;
        z-index:-1;
        transition: .2s;
    }

.btn-land:hover {
        border-color: #0000CD;
        box-shadow: 0px 3px 20px 0px black;
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        background-color: #FFD700;
        color:#252422;
        transition: all 0.3 ease-out;
}
